import DialogPay from "@/components/checkout/DialogPay";
import DialogDispute from "@/components/DialogDispute";
export default {
  data: function data() {
    return {
      loading: false,
      searchKey: "",
      pageSizes: [10, 20, 50, 100],
      page: this.$route.query.page ? Number(this.$route.query.page) : 1,
      rowsPerPage: localStorage.getItem("c_purchaseListPerPage") ? Number(localStorage.getItem("c_purchaseListPerPage")) : 10,
      total: 0,
      totalPage: 0,
      items: [],
      dialogPay: {
        isShow: false,
        loading: false,
        orderNum: "",
        amount: "",
        platformType: 3,
        payLoading: false,
        step: 1,
        msg: "",
        //出错
        purchaseOrderId: ""
      },
      dialogDispute: {
        isShow: false
      },
      dialogPayDefault: "{}",
      dialogDisputeDefault: "{}",
      iptCode: "",
      iptSku: "",
      iptName: "",
      filterParams: {
        code: "",
        sku: "",
        name: ""
      },
      filterParamsDefault: "{}"
    };
  },
  components: {
    DialogPay: DialogPay,
    DialogDispute: DialogDispute
  },
  watch: {
    $route: "gotoPage"
  },
  mounted: function mounted() {
    this.dialogPayDefault = JSON.stringify(this.dialogPay);
    this.dialogDisputeDefault = JSON.stringify(this.dialogDispute);
    this.filterParamsDefault = JSON.stringify(this.filterParams);
    this.getItem();
  },
  methods: {
    productClick: function productClick(id) {
      this.$Burying({
        object: '7002',
        objectId: id
      });
    },
    toDetail: function toDetail(item) {
      this.$router.push({
        name: 'purchaseDetail',
        query: {
          code: item.code
        }
      });
    },
    openPay: function openPay(item) {
      //打开支付
      this.dialogPay = JSON.parse(this.dialogPayDefault);
      this.dialogPay.isShow = true;
      this.dialogPay.amount = Number(item.realAmount);
      this.dialogPay.orderNum = item.code;
      this.dialogPay.purchaseOrderId = item.id;
    },
    openDispute: function openDispute(item) {
      // 打开纠纷编辑框
      this.dialogDispute = JSON.parse(this.dialogDisputeDefault);
      this.dialogDispute.isShow = true;
      this.dialogDispute.orderNum = item.code;
      this.dialogDispute.status = item.status;
      this.dialogDispute.purchaseOrderId = item.id;
      this.$refs.DialogDispute.form = {
        reason: "",
        result: "",
        remark: ""
      };
    },
    handleCommand: function handleCommand(command) {
      var _this = this;

      console.log(command);

      switch (command.type) {
        case "pay":
          this.openPay(command.item);
          break;

        case "cancel":
          this.$confirm("Do you want to cancel the order?", "", {
            confirmButtonClass: " el-button--danger",
            type: "warning"
          }).then(function () {
            _this.loading = true;

            _this.$Burying({
              object: '13006',
              objectId: command.item.id
            });

            _this.$apiCall("api.PurchaseOrder.setCancel", {
              purchaseOrderId: command.item.id
            }, function (r) {
              if (r.ErrorCode == 9999) {
                _this.getItem(true);

                _this.$message({
                  message: "Order was successfully cancelled",
                  type: "success"
                });
              } else {
                _this.$message({
                  message: r.Message,
                  type: "error"
                });
              }
            });
          }).catch(function () {});
          break;

        case "dispute":
          this.openDispute(command.item);
          break;
      }
    },
    getItem: function getItem(s) {
      var _this2 = this;

      this.loading = true;
      var params = {
        page: this.page,
        rowsPerPage: this.rowsPerPage,
        code: this.filterParams.code,
        sku: this.filterParams.sku,
        name: this.filterParams.name
      };
      this.$apiCall("api.PurchaseOrder.findByCustomer", params, function (r) {
        _this2.loading = false;

        if (r.ErrorCode == 9999) {
          if (r.Data.Results.length) _this2.items = r.Data.Results.map(function (e) {
            if (e.shippingJson) {
              e.shippingJSONObj = JSON.parse(e.shippingJson);
            } else {
              e.shippingJSONObj = {};
            }

            return e;
          });
          _this2.total = Number(r.Data.Pagination.totalCount);
          _this2.totalPage = Number(r.Data.Pagination.totalPage);
          if (!s) _this2.$root.$children[0].$refs.mainScroll.wrap.scrollTop = 0;

          if (_this2.$route.params.action == 'pay') {
            if (_this2.items[0].status == 1) {
              _this2.openPay(_this2.items[0]);
            }
          }
        } else {
          _this2.$message({
            message: r.Message,
            type: "error"
          });
        }
      });
    },
    filterGetItem: function filterGetItem(type) {
      switch (type) {
        case "code":
          this.filterParams.code = this.iptCode;
          break;

        case "sku":
          this.filterParams.sku = this.iptSku;
          break;

        case "name":
          this.filterParams.name = this.iptName;
          break;

        default:
          this.filterParams.code = this.iptCode;
          this.filterParams.sku = this.iptSku;
          this.filterParams.name = this.iptName;
      }

      if (this.$route.query.page == 1) {
        this.getItem();
      } else {
        this.$router.push({
          query: {
            page: 1
          }
        });
      }
    },
    clearFilter: function clearFilter(type) {
      switch (type) {
        case "code":
          this.filterParams.code = this.iptCode = "";
          break;

        case "sku":
          this.filterParams.sku = this.iptSku = "";
          break;

        case "code":
          this.filterParams.name = this.iptName = "";
          break;

        default:
          this.iptCode = this.iptSku = this.iptName = '';
          this.filterParams = JSON.parse(this.filterParamsDefault);
      }

      if (this.$route.query.page == 1) {
        this.getItem();
      } else {
        this.$router.push({
          query: {
            page: 1
          }
        });
      }
    },
    toPage: function toPage(val) {
      if (val != this.$route.query.page) this.$router.push({
        query: {
          page: val
        }
      });
    },
    changePageSize: function changePageSize(val) {
      this.rowsPerPage = val;
      localStorage.setItem("c_purchaseListPerPage", val);
      this.getItem();
    },
    gotoPage: function gotoPage() {
      this.page = this.$route.query.page ? this.$route.query.page : 1;
      this.getItem();
    },
    formatAttr: function formatAttr(attr) {
      if (!attr) {
        return '';
      }

      attr = attr.split("||");
      attr = attr.filter(function (s) {
        return s && s.trim();
      });
      var str = attr.join(',');
      return str;
    }
  }
};