import checkout from '@/components/checkout/checkout';
export default {
  props: ["dialogPay"],
  data: function data() {
    return {
      dialogCheckOut: {
        isShow: false,
        loading: false,
        packages: null,
        orderPay: null,
        token: ''
      },
      dialogCheckOutDefault: '{}',
      payTime: 0,
      newWin: null
    };
  },
  components: {
    checkout: checkout
  },
  mounted: function mounted() {
    this.dialogCheckOutDefault = JSON.stringify(this.dialogCheckOut);
  },
  methods: {
    payCheckOut: function payCheckOut(token) {
      var _this = this;

      this.dialogCheckOut.loading = true;
      this.newWin = window.open((this.$root.$children[0].baseUrl == '/my' ? '/my' : '') + '/blank.html', 'pay');
      this.$apiCall('api.PurchaseOrder.createPay', {
        purchaseOrderId: this.dialogPay.purchaseOrderId,
        platformType: 4,
        token: token
      }, function (r) {
        _this.dialogCheckOut.loading = false;
        _this.dialogCheckOut.isShow = false;

        if (r.ErrorCode == "9999" && r.Data.Results.url) {
          _this.newWin.location = r.Data.Results.url;
          _this.dialogPay.step = 2;
          _this.payTime = setInterval(function () {
            _this.getPayStatus();
          }, 2000);
        } else {
          _this.$elementMessage(r.ErrorCode == "9999" ? 'fail' : r.Message, 'error');

          _this.newWin.close();
        }
      });
    },
    orderPay: function orderPay() {
      var _this2 = this;

      //支付弹层支付
      if (this.dialogPay.platformType == 4) {
        this.dialogCheckOut = JSON.parse(this.dialogCheckOutDefault);
        this.dialogCheckOut.isShow = true;
        this.dialogCheckOut.orderPay = {
          orderNum: this.dialogPay.code,
          amount: Number(this.dialogPay.amount)
        };
        return;
      }

      this.dialogPay.payLoading = true;
      this.newWin = window.open((this.$root.$children[0].baseUrl == '/my' ? '/my' : '') + '/blank.html', 'pay');
      this.$apiCall('api.PurchaseOrder.createPay', {
        purchaseOrderId: this.dialogPay.purchaseOrderId,
        platformType: 3
      }, function (r) {
        _this2.dialogPay.payLoading = false;

        if (r.ErrorCode == "9999") {
          _this2.newWin.location = r.Data.Results.url;
          _this2.dialogPay.step = 2;
          _this2.payTime = setInterval(function () {
            _this2.getPayStatus();
          }, 2000);
        } else {
          _this2.$elementMessage(r.Message, 'error');

          _this2.newWin.close();
        }
      });
    },
    getPayStatus: function getPayStatus() {
      var _this3 = this;

      //获取支付状态
      this.$apiCall('api.User.getPayStatus', {}, function (r) {
        if (r.ErrorCode == "9999") {
          var status = r.Data.Results.status;

          if (status != 1) {
            clearInterval(_this3.payTime);
          }

          if (status == 2) {
            //支付成功
            _this3.$Burying({
              object: '13008'
            });

            _this3.dialogPay.step = 3;

            _this3.closePayPage();
          } else if (status == 3) {
            //支付失败
            _this3.dialogPay.step = 3;
            _this3.dialogPay.msg = r.Data.Results.message;

            _this3.$elementMessage(r.Data.Results.message, 'error');

            _this3.closePayPage();
          }
        } else {
          _this3.dialogPay.msg = r.Message;

          _this3.$elementMessage(r.Message, 'error');

          clearInterval(_this3.payTime);
        }
      });
    },
    changePlatform: function changePlatform(type) {
      this.dialogPay.platformType = type;
    },
    closePay: function closePay() {
      this.newWin.close();
      window.location.reload();
    },
    closePayPage: function closePayPage() {
      //关闭支付新开页面
      this.newWin.close();
      clearInterval(this.payTime);
    },
    handlePayClose: function handlePayClose() {
      this.dialogPay.isShow = false;
    }
  }
};